<template>
    <template v-if="data.items?.length">
        <draggable
            v-model="data.items"
            :item-key="(item) => item.id || item.code"
            tag="ul"
            handle=".sort-button"
            group="widgets"
            ghost-class="dragging"
            class="items"
        >
            <template #item="{ element: item }">
                <li :class="{ active: props.id === item.id }">
                    <div class="row"
                        v-if="item.id"
                        @click="emit('open', item)"
                    >
                        <Button icon="drag" class="tertiary submenu size-m sort-button" />

                        <ShowcaseWidgetPreview :item="item" />

                        <IconButton :icon="item.showed ? 'eye-opened' : 'eye-closed'"
                            class="tertiary size-m"
                            @click="toggleShowed(item)"
                        />

                        <IconButton icon="close"
                            :disabled="!!item.childs?.length"
                            class="tertiary size-m"
                            @click="remove(item)"
                        />
                    </div>

                    <div class="row empty"
                        v-else
                    >
                        <ShowcaseWidgetAutoComplete
                            :maxValuesCount="1"
                            :selectOnly="true"
                            @change="onSelect"
                        >
                            <div class="trigger-box">
                                <IconButton icon="plus" class="tertiary submenu size-m" />

                                <Img v-if="defaultWidgetImage?.resized"
                                    :preview="defaultWidgetImage?.resized"
                                    :original="defaultWidgetImage?.original"
                                    :alt="'Add subwidget'"
                                />
                            </div>
                        </ShowcaseWidgetAutoComplete>
                    </div>

                    <ShowcaseLineupWidgets v-if="item.id"
                        :items="item.childs"
                        :id="props.id"
                        :parentId="item.id"
                        :sectionId="props.sectionId"
                        @open="emit('open', $event)"
                        @change="onChildChange"
                    />
                </li>
            </template>
        </draggable>
    </template>
</template>

<script setup>
    import { reactive, watch, computed } from 'vue';
    import { useI18n } from 'vue-i18n';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import Img from 'ðŸ”—/components/ui/Img.vue';
    import IconButton from 'ðŸ”—/components/ui/IconButton.vue';
    import ShowcaseWidgetPreview from './ShowcaseWidgetPreview.vue';
    import ShowcaseLineupWidgets from "./ShowcaseLineupWidgets.vue";
    import ShowcaseWidgetAutoComplete from './ShowcaseWidgetAutoComplete.vue';
    import KebabMenu from 'ðŸ”—/components/ui/KebabMenu.vue';
    import { code } from 'ðŸ”—/utils/utils';

    import draggable from 'vuedraggable'
    import api from 'ðŸ”—/api';

    const emit = defineEmits(['change', 'open']);

    const { t } = useI18n();

    const props = defineProps({
        id: { type: [String, null], default: null },
        parentId: { type: [String, null], default: null },
        sectionId: { type: [String, null], default: null },
        items: { type: [Array, null], default: null }
    });

    const data = reactive({
        items: [...(props.items || []), { code: code() }]
    });

    const defaultWidgetImage = computed(() => {
        return api.showcase().defaultWidgetCover;
    });

    const onChange = () => {
        emit('change', {
            sectionId: props.sectionId,
            id: props.parentId,
            childs: data.items.filter(item => !!item.id)
        })
    }

    const onChildChange = (payload) => {
        if (data.items) {
            data.items.forEach(item => {
                if (item.id === payload?.id) {
                    item.childs = payload.childs
                }
            });

            onChange();
        }
    }

    const update = (id, params) => {
        api.showcase().widget.update(id, params);
    }

    const toggleShowed = (item) => {
        if (!item?.id) {
            return;
        }

        item.showed = !item.showed;

        update(item.id, { showed: item.showed });

        onChange();
    }

    const remove = (payload) => {
        update(payload.id, { parentId: null, sectionId: null })

        data.items.splice(data.items.indexOf(payload), 1);

        onChange();
    }

    const onSelect = (e) => {
        if (!e?.values?.length) {
            return;
        }

        data.items.splice(data.items.length - 1, 0, e.values[0]);

        update(e.values[0].id, {
            parentId: props.parentId,
            sectionId: props.sectionId,
            position: data.items.length
        });


        onChange();
    }

    watch(
        () => data.items,
        (newValue, oldValue) => {
            const emptyIndex = data.items.findIndex(item => !item.id);
            if (emptyIndex !== data.items.length - 1 && emptyIndex !== -1) {
                data.items.push(...data.items.splice(emptyIndex, 1));
            }

            newValue.filter(item => !!item.id).forEach((item, id) => {
                if (
                    (item.parentId !== props.parentId) ||
                    (item.sectionId !== props.sectionId) ||
                    (item.position !== id + 1)) {
            console.log(item, id);

                    update(item.id, {
                        parentId: props.parentId,
                        sectionId: props.sectionId,
                        position: id + 1
                    });
                }
            });

            onChange();
        }
    );
</script>

<style lang="scss" scoped>
    // @include studio.default-items-list;

    .items-list {
        .items {
            padding: 0 0 0 32px !important;
            width: 100%;

            li {
                display: flex;
                flex-direction: column;
                gap: 4px;
                padding: 0 !important;

                & > .row {
                    display: flex;
                    flex-direction: row;
                    gap: 8px;
                    width: 100%;
                    min-height: 45px;
                    padding: 0 8px;
                    align-items: center;

                    button {
                        flex-shrink: 0;
                    }

                    .sort-button {
                        // margin-left: -6px;
                        cursor: grab;
                    }

                    .item-preview {
                        padding-right: 8px;
                        // .wathermark,
                        // img {
                        //     width: 46px;
                        //     height: 46px;
                        // }
                    }

                    &.empty {
                        min-height: 38px;
                        width: 100%;
                        opacity: .4;

                        .autocomplete-field {
                            width: 100%;
                        }

                        .trigger-box {
                            display: flex;
                            align-items: center;
                            flex-direction: row;
                            flex: 1;

                            img {
                                width: 24px;
                                height: 24px;
                                border-radius: 8px;
                            }
                        }

                        &:hover {
                            opacity: 1;
                        }
                    }

                    &:hover {
                        background: var(--light-10);
                    }
                }

                &.active {
                    & > .row {
                        background: var(--light-20);
                    }
                }


                // &.dragging + li {
                //     & > .empty {
                //         background: var(--dark-20);
                //     }
                // }
            }
        }
    }
</style>