<template> 
    <Teleport to="#head-buttons">
        <KebabMenu
            icon="plus"
            v-if="user.isShowcaseCurator && props.item?.id"
            :leftAuto="!locale.rtl"
            :rightAuto="locale.rtl"
            :tertiary="true"
            :invert="true"
            size="size-m"
            @toggle="data.addKebabActive = !data.addKebabActive"
        >
            <template v-slot:trigger>
                <Button 
                    icon="plus"
                    :name="addButtonName"
                    :class="{
                        active: data.addKebabActive,
                        invert: true,
                        tertiary: true,
                        'size-s': addButtonName,
                        'size-m': !addButtonName
                    }"
                    :kebabTrigger="!!addButtonName"
                />
            </template>
            <template v-slot>
                <ul>
                    <template v-if="!data.items?.length">
                        <li @click="selectPlaylists">
                            <Icon icon="plus" />
                            <span>{{ t('pages.showcase.lineup.types.playlist.add') }}</span>
                        </li>

                        <li @click="selectReleases">
                            <Icon icon="plus" />
                            <span>{{ t('pages.showcase.lineup.types.release.add') }}</span>
                        </li>

                        <li @click="selectTracks">
                            <Icon icon="plus" />
                            <span>{{ t('pages.showcase.lineup.types.track.add') }}</span>
                        </li>

                        <li @click="selectArtists">
                            <Icon icon="plus" />
                            <span>{{ t('pages.showcase.lineup.types.artist.add') }}</span>
                        </li>
                    </template>
                    <template v-else>
                        <li @click="selectToBeginning">
                            <Icon icon="plus" />
                            <span>{{ t('pages.showcase.lineup.add.beginning') }}</span>
                        </li>
                        <li @click="selectToEnd">
                            <Icon icon="plus" />
                            <span>{{ t('pages.showcase.lineup.add.end') }}</span>
                        </li>
                    </template>
                </ul>
            </template>
        </KebabMenu>
    </Teleport>

   <div class="items-list">
        <template v-if="data.items.length">
            <draggable
                v-model="data.items"
                :item-key="(item) => item.id"
                tag="ul"
                handle=".sort-button"
                ghost-class="dragging"
                @end="onSortEnd"
                class="items"
            >
                <template #item="{ element: item }">
                    <li>
                        <Button icon="drag"
                            class="tertiary submenu size-m sort-button"
                        />

                        <PlaylistPreview v-if="data.type === 'playlist'"
                            :item="item"
                            :row="true"
                            :showState="true"
                        />

                        <TrackPreview v-if="data.type === 'track'"
                            :item="item"
                            :showState="true"
                        />

                        <ReleasePreview v-if="data.type === 'release'"
                            :item="item"
                            :row="true"
                            :showState="true"
                        />

                        <ArtistPreview v-if="data.type === 'artist'"
                            :item="item"
                            :row="true"
                            :showState="true"
                        />

                        <IconButton icon="close"
                            class="tertiary size-m"
                            @click="remove(item)"
                        />
                    </li>
                </template>
            </draggable>
        </template>

        <EmptyLabel
            v-if="!data.items.length"
            class="empty"
            :title="t(`pages.showcase.lineup.empty.title`)"
            :description="t(`pages.showcase.lineup.empty.description`)"
        />
    </div>
</template>

<script setup>
    import { onMounted, reactive, computed, watch } from 'vue';
    import { useI18n } from 'vue-i18n';
    import draggable from 'vuedraggable'
    import api from 'ðŸ”—/api';
    import stores from '@/stores';

    import EmptyLabel from 'ðŸ”—/components/ui/EmptyLabel.vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import KebabMenu from 'ðŸ”—/components/ui/KebabMenu.vue';
    import IconButton from 'ðŸ”—/components/ui/IconButton.vue';

    import TrackPreview from 'ðŸ”—/components/ui/TrackPreview.vue';
    import PlaylistPreview from 'ðŸ”—/components/ui/PlaylistPreview.vue';
    import ReleasePreview from 'ðŸ”—/components/ui/ReleasePreview.vue';
    import ArtistPreview from 'ðŸ”—/components/ui/ArtistPreview.vue';


    const emit = defineEmits(['update', 'add', 'edit']);
    const locale = stores.locale();
    const user = stores.user();
    const modals = stores.modals();

    const { t } = useI18n();

    const props = defineProps({
        item: { type: [Object, null], default: null }
    });

    const data = reactive({
        // playlists: [],
        // tracks: [],
        // releases: [],
        // artists: [],
        type: null,
        items: []
    });

    window.q = props;

    const addButtonName = computed(() => {
        if (!data.type) {
            return;
        }

        return t(`pages.showcase.lineup.types.${data.type}.add`);
    });

    watch(
        () => data.items,
        () => {
            checkPosition();
        }
    );

    // Remove
        const remove = (item) => {
            // Playlist
            if (data.type === 'playlist') {
                api.showcase().widget.playlist.remove({
                    widgetId: props.item?.id,
                    playlistId: item?.id
                })
            }

            // Release
            if (data.type === 'release') {
                api.showcase().widget.release.remove({
                    widgetId: props.item?.id,
                    releaseId: item?.id
                })
            }

            // Track
            if (data.type === 'track') {
                api.showcase().widget.track.remove({
                    widgetId: props.item?.id,
                    trackId: item?.id
                })
            }

            // Artist
            if (data.type === 'artist') {
                api.showcase().widget.artist.remove({
                    widgetId: props.item?.id,
                    artistId: item?.id
                })
            }

            data.items.splice(data.items.indexOf(item), 1);
            checkPosition();
        }

    // Position
        const checkPosition = () => {
            clearTimeout(data.checkPositionTimeout);

            data.checkPositionTimeout = setTimeout(() => {
                delete data.checkPositionTimeout;

                let alreadySended = false;


                data.items.filter(item => !!item.id).forEach((item, id) => {
                    if (item.position !== id + 1) {

                        item.position = id + 1;

                        if (!alreadySended) {
                            // Playlist
                            if (data.type === 'playlist') {
                                api.showcase().widget.playlist.setPosition({
                                    widgetId: props.item?.id,
                                    playlistId: item?.id,
                                    position: id + 1 
                                })
                            }

                            // Release
                            if (data.type === 'release') {
                                api.showcase().widget.release.setPosition({
                                    widgetId: props.item?.id,
                                    releaseId: item?.id,
                                    position: id + 1 
                                })
                            }

                            // Track
                            if (data.type === 'track') {
                                api.showcase().widget.track.setPosition({
                                    widgetId: props.item?.id,
                                    trackId: item?.id,
                                    position: id + 1 
                                })
                            }

                            // Artist
                            if (data.type === 'artist') {
                                api.showcase().widget.artist.setPosition({
                                    widgetId: props.item?.id,
                                    artistId: item?.id,
                                    position: id + 1 
                                })
                            }

                            alreadySended = true;
                        }
                    }
                });
            }, 100);
        }

    // Add
        const add = (payload, mode) => {
            if (!payload?.id) {
                return;
            }

            if (data.items.find(item => item.id === payload.id)) {
                return;
            }

            if (mode === 'beginning') {
                data.items.unshift(payload);
            } else {
                data.items.push(payload);
            }

            checkPosition();
        }

        const addPlaylist = async (payload, mode) => {
            if (!data.type) {
                data.type = 'playlist';
            }

            await api.showcase().widget.playlist.add({
                widgetId: props.item?.id,
                playlistId: payload?.id
            })

            add(payload, mode);
        }

        const addTrack = async (payload, mode) => {
            if (!data.type) {
                data.type = 'track';
            }

            await api.showcase().widget.track.add({
                widgetId: props.item?.id,
                trackId: payload?.id
            })

            add(payload, mode);
        }

        const addRelease = async (payload, mode) => {
            if (!data.type) {
                data.type = 'release';
            }

            await api.showcase().widget.release.add({
                widgetId: props.item?.id,
                releaseId: payload?.id
            })

            add(payload, mode);
        }

        const addArtist = async (payload, mode) => {
            if (!data.type) {
                data.type = 'artist';
            }

            await api.showcase().widget.artist.add({
                widgetId: props.item?.id,
                artistId: payload?.id
            })

            add(payload, mode);
        }

    // Select
        const selectPlaylists = (mode) => {
            modals.open('select-playlists', {
                data: {
                    items: data.items
                },
                callback: (items) => {
                    if (!items?.length) {
                        return;
                    }

                    if (mode === 'beginning') {
                        items.reverse().forEach(item => addPlaylist(item, mode));
                    } else {
                        items.forEach(item => addPlaylist(item, mode));
                    }
                }
            });
        }

        const selectTracks = (mode) => {
            modals.open('select-tracks', {
                data: {
                    items: data.items
                },
                callback: (items) => {
                    if (!items?.length) {
                        return;
                    }

                    if (mode === 'beginning') {
                        items.reverse().forEach(item => addTrack(item, mode));
                    } else {
                        items.forEach(item => addTrack(item, mode));
                    }
                }
            });
        }

        const selectReleases = (mode) => {
            modals.open('select-releases', {
                data: {
                    items: data.items
                },
                callback: (items) => {
                    if (!items?.length) {
                        return;
                    }

                    if (mode === 'beginning') {
                        items.reverse().forEach(item => addRelease(item, mode));
                    } else {
                        items.forEach(item => addRelease(item, mode));
                    }
                }
            });
        }

        const selectArtists = (mode) => {
            modals.open('select-artists', {
                data: {
                    items: data.items
                },
                callback: (items) => {
                    if (!items?.length) {
                        return;
                    }

                    if (mode === 'beginning') {
                        items.reverse().forEach(item => addArtist(item, mode));
                    } else {
                        items.forEach(item => addArtist(item, mode));
                    }
                }
            });
        }

        const selectToBeginning = () => {
            const actions = {
                playlist: selectPlaylists,
                track: selectTracks,
                release: selectReleases,
                artist: selectArtists
            }

            if (actions[data.type]) {
                actions[data.type]('beginning');
            }
        }

        const selectToEnd = () => {
            const actions = {
                playlist: selectPlaylists,
                track: selectTracks,
                release: selectReleases,
                artist: selectArtists
            }

            if (actions[data.type]) {
                actions[data.type]('end');
            }
        }

    const onSortEnd = (evt) => {
        // const movedItem = props.items.splice(evt.oldIndex, 1)[0]
        // props.items.splice(evt.newIndex, 0, movedItem);

        // emit('sort', { 
        //     oldIndex: evt.oldIndex,
        //     newIndex: evt.newIndex,
        // });
    }

    onMounted(() => {
        if (props.item?.releases?.length) {
            data.type = 'release';
            data.items = props.item.releases;
        }

        if (props.item?.tracks?.length) {
            data.type = 'track';
            data.items = props.item.tracks;
        }

        if (props.item?.playlists?.length) {
            data.type = 'playlist';
            data.items = props.item.playlists;
        }

        if (props.item?.artists?.length) {
            data.type = 'artist';
            data.items = props.item.artists;
        }
    });
</script>

<style lang="scss" scoped>
    @include studio.default-items-list;

    .items-list {
        width: 100%;
        max-width: none;
        border: none;
        .items {
            li {
                padding: 8px;

                .sort-button {
                    margin-left: -6px;
                    cursor: grab;
                }
            }
        }
    }
</style>