<template>
    <AutoComplete
        v-bind="props"
        :optionWidth="320"
        :optionList="data.optionList || []"
        class="noerror"
        @search="onAutoCompleteSearch"
        @change="change"
        ref="autocomplete"
    >
        <template #trigger>
            <slot></slot>
        </template>
        <template #option="{ option }">
            <ShowcaseWidgetPreview :item="option"/>
        </template>
        <template #value="{ value }">
            <span class="name">{{ value.name }}</span>
        </template>
    </AutoComplete>
</template>

<script setup>
    import { reactive, onMounted, computed, ref } from 'vue';
    import AutoComplete from 'ðŸ”—/components/ui/AutoComplete.vue';
    import ShowcaseWidgetPreview from './ShowcaseWidgetPreview.vue';
    import api from 'ðŸ”—/api';
    import { code } from 'ðŸ”—/utils/utils';

    const emit = defineEmits(['change']);

    const autocomplete = ref(null);

    const props = defineProps({
        selectOnly: { type: Boolean, default: false }
    });

    const data = reactive({
        code: code(),
        optionList: []
    });

    const onAutoCompleteSearch = async (value) => {
        const searchStr = value?.toLowerCase().trim();

        const params = {
            skip: 0,
            take: 20,
            parentId: null,
            sort: 'name',
            sort_dir: 'asc'
        }

        if (searchStr?.trim()) {
            params.name = searchStr.trim().toLowerCase();
        }

        const payload = await api.showcase().widget.preview(params, data.code);

        if (!payload?.items) {
            data.optionList = [];
            return;
        }

        data.optionList = await payload.items.map(item => ({
            ...item,
            code: item.id,
        }));
    }

    const change = (e) => {
        if (data.lock) {
            return false;
        }

        emit('change', e);

        if (props.selectOnly) {
            data.lock = true;
            setTimeout(() => {
                clean();
                setTimeout(() => {
                    data.lock = false;
                });
            });
        }
    }

    const clean = () => {
        if (autocomplete?.value?.clean) {
            autocomplete?.value?.clean();
        }
    }

    onMounted(() => {
        onAutoCompleteSearch();
    })
</script>

<style lang="scss" scoped>
    .item-preview {
        min-width: 240px;
        padding: 0;
    }
</style>
