<template>
    <div class="showcase-widget-info" v-if="!data.loading" v-show="!data.updating">
        <label class="title">{{ t(`pages.showcase.widget.${ props.item?.id ? 'edit' : 'add' }.title`) }}</label>

        <!-- Name -->
        <div class="form-line">
            <label>{{ t(`pages.showcase.widget.fields.name`) }}</label>


            <MultilingualField
                :placeholder="t(`pages.showcase.widget.fields.name`)"
                :value="data.name"
                :disabled="!user.isShowcaseCurator"
                @change="onNameChange"
            />
        </div>

        <!-- Code -->
        <div class="form-line">
            <label>{{ t(`pages.showcase.widget.fields.code.name`) }}</label>

            <div class="box">
                <Field
                    :placeholder="t(`pages.showcase.widget.fields.code.name`)"
                    :value="data.code"
                    :disabled="!user.isShowcaseCurator"
                    class="noerror light with-placeholder"
                    @change="onCodeChange"/>

                <span class="attention">
                    <span>{{ t(`pages.showcase.widget.fields.code.subattention1`) }}</span>
                </span>
            </div>
        </div>

        <!-- Name -->
        <div class="form-line">
            <label>{{ t(`pages.showcase.widget.fields.description`) }}</label>


            <MultilingualField
                :placeholder="t(`pages.showcase.widget.fields.description`)"
                :value="data.description"
                :disabled="!user.isShowcaseCurator"
                type="textarea"
                @change="onDescriptionChange"
            />
        </div>

        <!-- Start date -->
        <div class="form-line">
            <label>{{ t(`pages.showcase.widget.fields.start-date.name`) }}</label>

            <div class="box">
                <DateField
                    :text-input="true"
                    :enableTimePicker="false"
                    :timePicker="false"
                    :placeholder="t(`pages.showcase.widget.fields.start-date.placeholder`)"
                    :isError="isDateError"
                    format="dd.MM.yyyy"
                    class="light noerror with-placeholder"
                    :disabled="!user.isShowcaseCurator"
                    :modelValue="data.startDate"
                    @update:modelValue="onStartDateChange"
                />

                <span class="attention">
                    <span>{{ t(`pages.showcase.widget.fields.start-date.subattention1`) }}</span>
                </span>
            </div>
        </div>

        <!-- End date -->
        <div class="form-line">
            <label>{{ t(`pages.showcase.widget.fields.end-date.name`) }}</label>

            <div class="box">
                <DateField
                    :text-input="true"
                    :enableTimePicker="false"
                    :timePicker="false"
                    :placeholder="t(`pages.showcase.widget.fields.end-date.placeholder`)"
                    :isError="isDateError"
                    format="dd.MM.yyyy"
                    class="light noerror with-placeholder"
                    :disabled="!user.isShowcaseCurator"
                    :modelValue="data.endDate"
                    @update:modelValue="onEndDateChange"
                />

                <span class="attention">
                    <span>{{ t(`pages.showcase.widget.fields.end-date.subattention1`) }}</span>
                </span>
            </div>
        </div>

        <!-- Template -->
        <div class="form-line">
            <label>{{ t(`pages.showcase.widget.fields.template`) }}</label>

            <Select
                class="noerror light"
                :optionList="SHOWCASE_WIDGET_TEMPLATES"
                :placeholder="t(`pages.showcase.widget.fields.template`)"
                :leftAuto="false"
                :maxValuesCount="1"
                :rightAuto="false"
                :useI18n="false"
                :clearable="false"
                :value="data.template"
                :disabled="!user.isShowcaseCurator"
                @change="onTemplateChange"
            />
        </div>


        <!-- Showed -->
        <div class="form-line">
            <Toggler
                :name="t(`pages.showcase.widget.fields.showed`)"
                :inline="true"
                :checked="data.showed"
                :disabled="!user.isShowcaseCurator"
                class="reverse solid"
                @onClick="onShowedChange"
            />
        </div>

        <!-- Page -->
        <!-- <div class="form-line">
            <label>{{ t(`pages.showcase.widget.fields.page`) }}</label>

            <Select
                class="noerror light"
                :optionList="SHOWCASE_PAGES"
                :placeholder="t(`pages.showcase.widget.fields.page`)"
                :leftAuto="false"
                :maxValuesCount="1"
                :rightAuto="false"
                :useI18n="true"
                :clearable="false"
                :value="data.page"
                :disabled="!user.isShowcaseCurator"
                @change="onPageChange"
            />
        </div> -->

        <!-- Type -->
        <div class="form-line">
            <label>{{ t(`pages.showcase.widget.fields.type`) }}</label>

            <Select
                class="noerror light"
                :optionList="SHOWCASE_WIDGET_TYPE"
                :placeholder="t(`pages.showcase.widget.fields.type`)"
                :leftAuto="false"
                :maxValuesCount="1"
                :rightAuto="false"
                :useI18n="true"
                :clearable="false"
                :value="data.type"
                :disabled="!user.isShowcaseCurator"
                @change="onTypeChange"
            />
        </div>


        <!-- Cover -->
        <div class="form-line">
            <label>
                <span>
                    {{ t(`pages.showcase.widget.fields.cover.name`) }}
                </span>
                <span class="attention">
                    {{ t(`pages.showcase.widget.fields.cover.description`) }}
                </span>
            </label>

            <div class="img-box">
                <FileUploader 
                    class="upload"
                    icon="upload-img"
                    :acceptedFormats="['image/png', 'image/jpeg', 'image/jpg', 'image/gif', 'image/jfif']"
                    :loading="data.loading"
                    :dropZone="true"
                    :showPreview="true"
                    :preview="data.cover?.resized"
                    :zoneSize="{ width: '120px', height: '120px' }"
                    zoneType="square-image"
                    @change="onCoverChange"
                />
            </div>
        </div>

        <!-- Header Cover -->
        <div class="form-line">
            <label>
                <span>
                    {{ t(`pages.showcase.widget.fields.header-cover.name`) }}
                </span>
                <span class="attention">
                    {{ t(`pages.showcase.widget.fields.header-cover.description`) }}
                </span>
            </label>

            <div class="img-box">
                <FileUploader 
                    class="upload"
                    icon="upload-img"
                    :acceptedFormats="['image/png', 'image/jpeg', 'image/jpg', 'image/gif', 'image/jfif']"
                    :loading="data.loading"
                    :dropZone="true"
                    :showPreview="true"
                    :preview="data.headerCover?.resized"
                    :zoneSize="{ width: '226px', height: '114px' }"
                    zoneType="square-image"
                    @change="onHeaderCoverChange"
                />
            </div>
        </div>
 
        <Teleport to="#head-buttons">
            <Button
                v-if="data.id"
                icon="trash"
                :name="t(`pages.showcase.widget.remove.title`)"
                class="size-s tertiary invert"
                @click="remove"
            />

            <Button :name="t(`pages.showcase.widget.${ props.item?.id ? 'edit' : 'add' }.save`)"
                class="size-s secondary"
                :disabled="saveDisabled"
                @click="saveChanges"
            />
        </Teleport>
    </div>
    <div class="loader" v-if="data.loading || data.updating">
        <div>
            <Loader :inside="true" class="tertiary" />
        </div>
    </div>
</template>

<script setup>
    import { reactive, computed, onMounted } from 'vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import { useI18n } from 'vue-i18n';
    import api from 'ðŸ”—/api';
    import MultilingualField from 'ðŸ”—/components/ui/MultilingualField.vue';
    import Field from 'ðŸ”—/components/ui/Field.vue';
    import FileUploader from 'ðŸ”—/components/ui/FileUploader.vue';
    import Select from 'ðŸ”—/components/ui/Select.vue';
    import Toggler from 'ðŸ”—/components/ui/Toggler.vue';
    import Loader from 'ðŸ”—/components/ui/Loader.vue';
    import DateField from 'ðŸ”—/components/ui/DateField.vue';
    import { delay, message } from 'ðŸ”—/utils/utils';
    import stores from '@/stores';
    import moment from 'moment';

    import { SHOWCASE_PAGES, SHOWCASE_WIDGET_TEMPLATES, SHOWCASE_WIDGET_TYPE } from 'ðŸ”—/constants';

    const modals = stores.modals();
    const user = stores.user();

    const emit = defineEmits(['update', 'add', 'remove', 'edit']);
    const { t } = useI18n();

    const props = defineProps({
        item: { type: [Object, null], default: null }
    });

    const data = reactive({
        id: null,
        name: null,
        description: null,

        cover: {},
        newCover: null,
        headerCover: {},
        newHeaderCover: null,

        code: null,
        // page: SHOWCASE_PAGES[0].code,
        type: SHOWCASE_WIDGET_TYPE[0].code,
        template: null,
        position: null,
        startDate: null,
        endDate: null,
        showed: false,
        loading: true
    });

    const saveDisabled = computed(() => {
        return !data.name || !data.name['en-US']?.trim().length || isDateError.value;
    });

    const isDateError = computed(() => {
        return data.startDate && data.endDate && moment(data.startDate).diff(moment(data.endDate)) >= 0;
    })


    const onNameChange = (value) => {
        data.name = value || {};
        onEdit();
    }

    const onDescriptionChange = (value) => {
        data.description = value || {};
        onEdit();
    }

    // const onPageChange = (payload) => {
    //     data.page = payload?.value;

    //     onEdit();
    // }

    const onTemplateChange = (payload) => {
        data.template = payload?.value;

        onEdit();
    }

    const onTypeChange = (payload) => {
        data.type = payload?.value;

        onEdit();
    }

    const onCodeChange = (e) => {
        data.code = e.target.value?.trim();
        onEdit();
    }

    const onShowedChange = (e) => {
        data.showed = !!e?.value;

        onEdit();
    }

    const onStartDateChange = (value) => {
        if (!value) {
            data.startDate = null;
        } else {
            data.startDate = moment(value).startOf('day').toISOString();
        }

        onEdit();
    }

    const onEndDateChange = (value) => {
        if (!value) {
            data.endDate = null;
        } else {
            data.endDate = moment(value).endOf('day').toISOString();
        }

        onEdit();
    }

    const onCoverChange = (e) => {
        if (e?.name) {
            data.newCover = e;
            onEdit();
        }
    }

    const onHeaderCoverChange = (e) => {
        if (e?.name) {
            data.newHeaderCover = e;
            onEdit();
        }
    }

    

    const onEdit = () => {
        emit('edit');
        data.hasChanges = true;
    }

    const saveChanges = async () => {
        const showcaseSectionInfo = {
            name: data.name,
            description: data.description,
            code: data.code,
            position: data.position,
            // page: data.page || null,
            type: data.type || null,
            template: data.template || null,
            startDate: data.startDate || null,
            endDate: data.endDate || null,
            showed: !!data.showed
        }

        data.updating = true;

        let payload;

        if (data.id) {
            payload = await api.showcase().widget.update(data.id, showcaseSectionInfo);
        } else {
            if (!data.position) {
                const result = await api.showcase().widget.preview({ skip: 0, take: 1 });
                if (result?.items) {
                    showcaseSectionInfo.position = (result.total || 0) + 1;
                    data.position = (result.total || 0) + 1;
                }
            }
            payload = await api.showcase().widget.add(showcaseSectionInfo);
        }


        // Cover
        if (payload?.id && data.newCover) {
            payload = await api.showcase().widget.updateCover(payload.id, data.newCover)
        }

        // HeaderCover
        if (payload?.id && data.newHeaderCover) {
            payload = await api.showcase().widget.updateHeaderCover(payload.id, data.newHeaderCover)
        }


        await delay(200);
        data.updating = false;

        if (payload?.id) {
            message.info(t(`pages.showcase.widget.${ data.id ? 'edit' : 'add' }.done.title`));
            // modals.open('success', {
            //     data: {
            //         timer: 3,
            //         title: t(`pages.showcase.widget.${ data.id ? 'edit' : 'add' }.done.title`),
            //         close: t(`pages.showcase.widget.${ data.id ? 'edit' : 'add' }.done.close`)
            //     },
            //     quietClose: true
            // });
        }

        if (data.id) {
            emit('update', payload);
        } else {
            emit('add', payload);
        }

        data.id = payload.id;

        data.cover = payload.cover || {};
        data.headerCover = payload.headerCover || {};

        data.newCover = null;
        data.newHeaderCover = null;

        delete data.hasChanges;
    }

    const getItem = async () => {
        data.loading = true;

        const payload = await api.showcase().widget.get(props.item.id);

        data.id = payload?.id;
        data.name = payload?.name || {};
        data.description = payload?.description || {};
        data.code = payload?.code;
        data.position = payload?.position;
        data.template = payload?.template;
        data.type = payload?.type;
        data.startDate = payload?.startDate;
        data.endDate = payload?.endDate;
        data.showed = !!payload?.showed;

        data.cover = payload?.cover || {};
        data.headerCover = payload?.headerCover || {};

        await delay(200);

        data.loading = false;

        delete data.hasChanges;
    }

    const remove = async () => {
        modals.confirm({
            title: t(`pages.showcase.widget.remove.confirm.title`),
            body: t(`pages.showcase.widget.remove.confirm.description`, { name: data.name ? data.name['en-US'] : '' }),
            onOk: async () => {
                await api.showcase().widget.remove(data.id);

                emit('remove', data.id);
                
                modals.open('success', {
                    data: {
                        title: t(`pages.showcase.widget.remove.done.title`),
                        close: t(`pages.showcase.widget.remove.done.close`)
                    },
                    quietClose: true
                });
            }
        }); 
    }

    onMounted(() => {
        if (props.item?.id) {
            getItem();
        } else {
            data.loading = false;
        }
    });
</script>

<style lang="scss" scoped>
    .loader {
        @include inside-loader;
    }

    .showcase-widget-info {
        display: flex;
        flex-direction: column;
        gap: 24px;
        .title {
            font: var(--font-h3);
            font-weight: bold;
            color: var(--dark-500);
        }
    }

    .form-line {
        @include studio.form-line();

        .img-box {
            position: relative;
            background-size: cover;
            background-position: center;
            border-radius: 10px;
            img {

            }
        }
    }

    // .buttons {
    //     display: flex;
    //     flex-direction: row;
    //     align-items: center;
    //     gap: 8px;
    //     margin-top: 32px;
    // }
</style>
