<template>
    <div class="showcase-section-info" v-if="!data.loading" v-show="!data.updating">
        <label class="title">{{ t(`pages.showcase.section.${ props.item?.id ? 'edit' : 'add' }.title`) }}</label>

        <!-- Name -->
        <div class="form-line">
            <label>{{ t(`pages.showcase.section.fields.name`) }}</label>


            <MultilingualField
                :placeholder="t(`pages.showcase.section.fields.name`)"
                :value="data.name"
                :disabled="!user.isShowcaseCurator"
                @change="onNameChange"
            />
        </div>

        <!-- Code -->
        <div class="form-line">
            <label>{{ t(`pages.showcase.section.fields.code.name`) }}</label>

            <div class="box">
                <Field
                    :placeholder="t(`pages.showcase.section.fields.code.name`)"
                    :value="data.code"
                    :disabled="!user.isShowcaseCurator"
                    class="noerror light with-placeholder"
                    @change="onCodeChange"/>

                <span class="attention">
                    <span>{{ t(`pages.showcase.section.fields.code.subattention1`) }}</span>
                </span>
            </div>
        </div>

        <!-- Start date -->
        <div class="form-line">
            <label>{{ t(`pages.showcase.section.fields.start-date.name`) }}</label>

            <div class="box">
                <DateField
                    :text-input="true"
                    :enableTimePicker="false"
                    :timePicker="false"
                    :placeholder="t(`pages.showcase.section.fields.start-date.placeholder`)"
                    :isError="isDateError"
                    format="dd.MM.yyyy"
                    class="light noerror with-placeholder"
                    :disabled="!user.isShowcaseCurator"
                    :modelValue="data.startDate"
                    @update:modelValue="onStartDateChange"
                />

                <span class="attention">
                    <span>{{ t(`pages.showcase.section.fields.start-date.subattention1`) }}</span>
                </span>
            </div>
        </div>

        <!-- End date -->
        <div class="form-line">
            <label>{{ t(`pages.showcase.section.fields.end-date.name`) }}</label>

            <div class="box">
                <DateField
                    :text-input="true"
                    :enableTimePicker="false"
                    :timePicker="false"
                    :placeholder="t(`pages.showcase.section.fields.end-date.placeholder`)"
                    :isError="isDateError"
                    format="dd.MM.yyyy"
                    class="light noerror with-placeholder"
                    :disabled="!user.isShowcaseCurator"
                    :modelValue="data.endDate"
                    @update:modelValue="onEndDateChange"
                />

                <span class="attention">
                    <span>{{ t(`pages.showcase.section.fields.end-date.subattention1`) }}</span>
                </span>
            </div>
        </div>


        <!-- Showed -->
        <div class="form-line">
            <Toggler
                :name="t(`pages.showcase.section.fields.showed`)"
                :inline="true"
                :checked="data.showed"
                :disabled="!user.isShowcaseCurator"
                class="reverse solid"
                @onClick="onShowedChange"
            />
        </div>

        <!-- Page -->
        <div class="form-line">
            <label>{{ t(`pages.showcase.section.fields.page`) }}</label>

            <Select
                class="noerror light"
                :optionList="SHOWCASE_PAGES"
                :placeholder="t(`pages.showcase.section.fields.page`)"
                :leftAuto="false"
                :maxValuesCount="1"
                :rightAuto="false"
                :useI18n="true"
                :clearable="false"
                :value="data.page"
                :disabled="!user.isShowcaseCurator"
                @change="onPageChange"
            />
        </div>
 
        <Teleport to="#head-buttons">
            <Button
                v-if="data.id"
                icon="trash"
                :name="t(`pages.showcase.section.remove.title`)"
                class="size-s tertiary invert"
                @click="remove"
            />

            <Button :name="t(`pages.showcase.section.${ props.item?.id ? 'edit' : 'add' }.save`)"
                class="size-s secondary"
                :disabled="saveDisabled"
                @click="saveChanges"
            />
        </Teleport>
    </div>
    <div class="loader" v-if="data.loading || data.updating">
        <div>
            <Loader :inside="true" class="tertiary" />
        </div>
    </div>
</template>

<script setup>
    import { reactive, computed, onMounted } from 'vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import { useI18n } from 'vue-i18n';
    import api from 'ðŸ”—/api';
    import MultilingualField from 'ðŸ”—/components/ui/MultilingualField.vue';
    import Field from 'ðŸ”—/components/ui/Field.vue';
    import Select from 'ðŸ”—/components/ui/Select.vue';
    import Toggler from 'ðŸ”—/components/ui/Toggler.vue';
    import Loader from 'ðŸ”—/components/ui/Loader.vue';
    import DateField from 'ðŸ”—/components/ui/DateField.vue';
    import { delay, message } from 'ðŸ”—/utils/utils';
    import stores from '@/stores';
    import moment from 'moment';

    import { SHOWCASE_PAGES } from 'ðŸ”—/constants';

    const modals = stores.modals();
    const user = stores.user();

    const emit = defineEmits(['update', 'add', 'remove', 'edit']);
    const { t } = useI18n();

    const props = defineProps({
        item: { type: [Object, null], default: null }
    });

    const data = reactive({
        id: null,
        name: {},
        code: null,
        page: SHOWCASE_PAGES[0].code,
        position: null,
        startDate: null,
        endDate: null,
        showed: false,
        loading: true
    });

    const saveDisabled = computed(() => {
        return !data.name || !data.name['en-US']?.trim().length || isDateError.value;
    });

    const isDateError = computed(() => {
        return data.startDate && data.endDate && moment(data.startDate).diff(moment(data.endDate)) >= 0;
    })


    const onNameChange = (value) => {
        data.name = value || {};
        onEdit();
    }

    const onPageChange = (payload) => {
        data.page = payload?.value;

        onEdit();
    }

    const onCodeChange = (e) => {
        data.code = e.target.value?.trim();
        onEdit();
    }

    const onShowedChange = (e) => {
        data.showed = !!e?.value;

        onEdit();
    }

    const onStartDateChange = (value) => {
        if (!value) {
            data.startDate = null;
        } else {
            data.startDate = moment(value).startOf('day').toISOString();
        }

        onEdit();
    }

    const onEndDateChange = (value) => {
        if (!value) {
            data.endDate = null;
        } else {
            data.endDate = moment(value).endOf('day').toISOString();
        }

        onEdit();
    }

    const onEdit = () => {
        emit('edit');
        data.hasChanges = true;
    }

    const saveChanges = async () => {
        const showcaseSectionInfo = {
            name: data.name,
            code: data.code,
            position: data.position,
            page: data.page || null,
            startDate: data.startDate || null,
            endDate: data.endDate || null,
            showed: !!data.showed
        }

        data.updating = true;

        let payload;

        if (data.id) {
            payload = await api.showcase().section.update(data.id, showcaseSectionInfo);
        } else {
            if (!data.position) {
                const result = await api.showcase().section.list({ skip: 0, take: 1 });
                
                if (result?.items) {
                    showcaseSectionInfo.position = (result.total || 0) + 1;
                    data.position = (result.total || 0) + 1;
                }
            }
            payload = await api.showcase().section.add(showcaseSectionInfo);
        }

        await delay(200);
        data.updating = false;

        if (payload?.id) {
            message.info(t(`pages.showcase.section.${ data.id ? 'edit' : 'add' }.done.title`));
            // modals.open('success', {
            //     data: {
            //         timer: 3,
            //         title: t(`pages.showcase.section.${ data.id ? 'edit' : 'add' }.done.title`),
            //         close: t(`pages.showcase.section.${ data.id ? 'edit' : 'add' }.done.close`)
            //     },
            //     quietClose: true
            // });
        }

        if (data.id) {
            emit('update', payload);
        } else {
            emit('add', payload);
        }

        data.id = payload.id;

        delete data.hasChanges;
    }

    const getItem = async () => {
        data.loading = true;

        const payload = await api.showcase().section.get(props.item.id);

        data.id = payload?.id;
        data.name = payload?.name || {};
        data.code = payload?.code;
        data.page = payload?.page;
        data.startDate = payload?.startDate;
        data.position = payload?.position;
        data.endDate = payload?.endDate;
        data.showed = !!payload?.showed;

        await delay(200);

        data.loading = false;

        delete data.hasChanges;
    }

    const remove = async () => {
        modals.confirm({
            title: t(`pages.showcase.section.remove.confirm.title`),
            body: t(`pages.showcase.section.remove.confirm.description`, { name: data.name ? data.name['en-US'] : '' }),
            onOk: async () => {
                await api.showcase().section.remove(data.id);

                emit('remove', data.id);
                
                modals.open('success', {
                    data: {
                        title: t(`pages.showcase.section.remove.done.title`),
                        close: t(`pages.showcase.section.remove.done.close`)
                    },
                    quietClose: true
                });
            }
        }); 
    }

    onMounted(() => {
        if (props.item?.id) {
            getItem();
        } else {
            data.loading = false;
        }
    });
</script>

<style lang="scss" scoped>
    .loader {
        @include inside-loader;
    }

    .showcase-section-info {
        display: flex;
        flex-direction: column;
        gap: 24px;
        .title {
            font: var(--font-h3);
            font-weight: bold;
            color: var(--dark-500);
        }
    }

    .form-line {
        @include studio.form-line();
    }

    // .buttons {
    //     display: flex;
    //     flex-direction: row;
    //     align-items: center;
    //     gap: 8px;
    //     margin-top: 32px;
    // }
</style>
