<template>
    <div class="items-list">
        <ul class="items">
            <li v-for="(item, id) in data.items"
                :class="{ active: props.id === item.id }"
                @click="emit('change', item)"
            >
                <ShowcaseSectionPreview :item="item" />
            </li>
        </ul>

        <Button :loading="data.loading"
            class="tertiary size-l"
            :name="t('pages.showcase.section.load')"
            v-if="data.loading || (data.items.length < data.total)"
            @click="getItems"
        />

        <EmptyLabel
            v-if="!data.items.length && !data.loading"
            class="empty"
            :title="t(`pages.showcase.section.empty.title`)"
            :description="t(`pages.showcase.section.empty.description`)"
        />
    </div>
</template>

<script setup>
    import { onMounted, reactive } from 'vue';
    import { useI18n } from 'vue-i18n';
    import api from 'ðŸ”—/api';
    import EmptyLabel from 'ðŸ”—/components/ui/EmptyLabel.vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';

    import ShowcaseSectionPreview from './ShowcaseSectionPreview.vue';

    import moment from 'moment';

    const emit = defineEmits(['change']);

    const { t } = useI18n();

    const props = defineProps({
        id: { type: [String, null], default: null }
    });

    const data = reactive({
        items: [],
        loading: false,
        skip: 0,
        take: 20,
        total: 0
    });

    const getItems = async () => {
        data.loading = true;

        if (!data.skip) {
            data.items = [];
        }

        const payload = await api.showcase().section.list({
            skip: data.skip,
            take: data.take
        });


        setTimeout(() => {
            if (payload?.items?.length) {
                data.items.splice(data.items.length, 0, ...(payload.items || []));
            }

            data.skip = data.items.length;
            data.total = payload?.total || 0;

            data.loading = false;
        }, 200);
    };

    const reload = () => {
        data.skip = 0;
        getItems();
    };

    const add = (payload) => {
        data.items.unshift(payload);
        data.total += 1;
    }

    const update = (payload) => {
        if (!payload?.id) {
            return;
        }

        const item = data.items.find(item => item.id === payload.id);
        if (item) {
            data.items.splice(data.items.indexOf(item), 1, payload);
        }
    }

    const remove = (id) => {
        if (!id) {
            return;
        }

        const item = data.items.find(item => item.id === id);
        if (item) {
            data.items.splice(data.items.indexOf(item), 1);
            data.skip -= 1;
            data.total -= 1;
        }
    }

    defineExpose({
        remove,
        add,
        update,
        reload
    });

    onMounted(() => {
        getItems();
    });
</script>

<style lang="scss" scoped>
    @include studio.default-items-list;
</style>