<template>
    <div class="items-list">
        <draggable
            v-model="data.items"
            :item-key="(item) => item.id"
            tag="ul"
            handle=".sort-button"
            ghost-class="dragging"
            group="sections"
            @end=""
            class="items"
        >
            <template #item="{ element: item }">
                <li :class="{ active: props.id === item.id }">
                    <div class="row">
                        <Button icon="drag" class="tertiary submenu size-m sort-button" />

                        <ShowcaseSectionPreview :item="item" />

                        <IconButton :icon="item.showed ? 'eye-opened' : 'eye-closed'"
                            class="tertiary size-m"
                            @click="toggleShowed(item)"
                        />
                    </div>

                    <ShowcaseLineupWidgets
                        :items="item.childs"
                        :id="props.id"
                        :sectionId="item.id"
                        @open="emit('openWidget', $event)"
                    />
                </li>
            </template>
        </draggable>

        <Button :loading="data.loading"
            class="tertiary size-l"
            :name="t('pages.showcase.lineup.load')"
            v-if="data.loading"
        />

        <EmptyLabel
            v-if="!data.items.length && !data.loading"
            class="empty"
            :title="t(`pages.showcase.lineup.empty.title`)"
            :description="t(`pages.showcase.lineup.empty.description`)"
        />
    </div>
</template>

<script setup>
    import { reactive, watch, onMounted } from 'vue';
    import { useI18n } from 'vue-i18n';
    import EmptyLabel from 'ðŸ”—/components/ui/EmptyLabel.vue';
    import IconButton from 'ðŸ”—/components/ui/IconButton.vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import ShowcaseSectionPreview from './ShowcaseSectionPreview.vue';
    import ShowcaseLineupWidgets from './ShowcaseLineupWidgets.vue';

    import api from 'ðŸ”—/api';
    import draggable from 'vuedraggable'

    const emit = defineEmits(['openWidget']);

    const { t } = useI18n();

    const props = defineProps({
        id: { type: [String, null], default: null }
    });

    const data = reactive({
        items: [],
        loading: false
    });

    const update = (id, params) => {
        api.showcase().section.update(id, params);
    }

    const toggleShowed = (item) => {
        if (!item?.id) {
            return;
        }

        item.showed = !item.showed;

        update(item.id, { showed: item.showed });
    }

    watch(
        () => data.items,
        (newValue, oldValue) => {
            newValue.forEach((item, id) => {
                if (item.position !== id + 1) {
                    item.position = id + 1;
                    api.showcase().section.update(item.id, { position: item.position });
                }
            });
        }
    );

    onMounted(async() => {
        const payload = await api.showcase().section.list({
            skip: 0,
            take: 100
        });

        const widgets = (await api.showcase().widget.list({
            skip: 0,
            take: 100
        }))?.items || [];

        const getWidgets = (sectionId, parentId) => {
            return widgets.filter(item => item.sectionId === sectionId && item.parentId === parentId).map(item => ({
                ...item,
                childs: getWidgets(sectionId, item.id)
            })).sort((a, b) => a.position > b.position ? 1 : -1);
        }

        data.items = (payload.items || []).map(item => ({
            ...item,
            type: 'section',
            childs: getWidgets(item.id, null)
        })).sort((a, b) => a.position > b.position ? 1 : -1);
    });
</script>

<style lang="scss" scoped>
    @include studio.default-items-list;

    .items-list {
        width: 600px;
        max-width: 45%;
        .items {
            li {
                display: flex;
                flex-direction: column;
                gap: 4px;
                padding: 8px 0;

                & > .row {
                    display: flex;
                    flex-direction: row;
                    gap: 8px;
                    width: 100%;
                    min-height: 45px;
                    padding: 0 8px;
                    align-items: start;

                    .sort-button {
                        // margin-left: -6px;
                        cursor: grab;
                    }

                    .item-preview {
                        padding-right: 8px;
                        padding-top: 10px;
                        // .wathermark,
                        // img {
                        //     width: 46px;
                        //     height: 46px;
                        // }
                    }
                }
            }
        }
    }
</style>