<template>
    <PageHeader :title="t('pages.showcase.section.title')" :detail="!!data.item">
        <template #title>
            <div class="header">
                <label class="title">{{ t('pages.showcase.title') }}</label>
                <Menu :items="data.sections"
                    :code="data.section"
                    @change="setSection"
                />
            </div>
        </template>
        <template #default>
            <template v-if="data.section === 'sections'">
                <Button
                    v-if="!data.item && user.isShowcaseCurator"
                    icon="plus"
                    class="size-s"
                    :name="t('pages.showcase.section.add.title')"
                    @click="add"
                />
            </template>

            <template v-if="data.section === 'widgets'">
                <Button
                    v-if="!data.item && user.isShowcaseCurator"
                    icon="plus"
                    class="size-s"
                    :name="t('pages.showcase.widget.add.title')"
                    @click="add"
                />
            </template>

            <IconButton
                v-if="data.item"
                icon="close"
                class="size-s tertiary invert"
                :title="t('pages.showcase.cancel')"
                @click="close"
            />
        </template>
    </PageHeader>

    <div class="box" v-if="data.section === 'lineup'">
        <ShowcaseLineup :id="data.item?.id"
            ref="showcaseLineup"
            @openWidget="onSelectItem" />

        <ShowcaseLineupWidgetContent :item="data.item" v-if="data.item"
            @add="onAdd"
            @update="onUpdate"
            @edit="onEdit"
        />

    </div>

    <div class="box" v-if="data.section === 'sections'">
        <ShowcaseSections :id="data.item?.id"
            ref="showcaseSections"
            @change="onSelectItem" />
        
        <div v-if="data.item" class="item-detail">
            <ShowcaseSectionInfo :item="data.item"
                @add="onAdd"
                @update="onUpdate"
                @edit="onEdit"
                @remove="onRemove"
            />
        </div>
    </div>
    <div class="box" v-if="data.section === 'widgets'">
        <ShowcaseWidgets :id="data.item?.id"
            ref="showcaseWidgets"
            @change="onSelectItem" />

        <div v-if="data.item" class="item-detail">
            <ShowcaseWidgetInfo :item="data.item"
                @add="onAdd"
                @update="onUpdate"
                @edit="onEdit"
                @remove="onRemove"
            />
        </div>
    </div>
</template>

<script setup>
    import { reactive, ref } from 'vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import IconButton from 'ðŸ”—/components/ui/IconButton.vue';
    import PageHeader from '@/components/blocks/PageHeader/PageHeader.vue';
    import Menu from 'ðŸ”—/components/ui/Menu.vue';

    import ShowcaseLineup from './components/ShowcaseLineup.vue';
    import ShowcaseLineupWidgetContent from './components/ShowcaseLineupWidgetContent/ShowcaseLineupWidgetContent.vue';

    import ShowcaseSections from './components/ShowcaseSections.vue';
    import ShowcaseSectionInfo from './components/ShowcaseSectionInfo.vue';

    import ShowcaseWidgets from './components/ShowcaseWidgets.vue';
    import ShowcaseWidgetInfo from './components/ShowcaseWidgetInfo.vue';

    import stores from '@/stores';
    
    import { useI18n } from 'vue-i18n';
    const user = stores.user();
    const modals = stores.modals();


    const { t } = useI18n();

    const showcaseSections = ref(null);
    const showcaseWidgets = ref(null);
    const showcaseLineup = ref(null);

    const data = reactive({
        item: null,
        section: 'lineup',
        sections: [
            {
                code: 'lineup',
                name: 'pages.showcase.lineup.title'
            },{
                code: 'sections',
                name: 'pages.showcase.section.title'
            },
            {
                code: 'widgets',
                name: 'pages.showcase.widget.title'
            }
        ]
    });

    const setSection = (section) => {
        checkUnsaved(() => {
            close();

            data.section = section;
        });
    }


    const onSelectItem = (item) => {
        checkUnsaved(() => {
            close();

            setTimeout(() => {
                data.item = item?.id ? item : null;
            });
        });
    }

    const close = () => {
        checkUnsaved(() => {
            data.item = null;
        });
    }

    const add = () => {
        checkUnsaved(() => {
            close();

            setTimeout(() => {
                data.item = {};
            });
        });
    }

    const checkUnsaved = async (callback) => {
        if (data.hasChanges) {
            let code = 'section';

            if (data.section === 'widgets') {
                code = 'widget';
            }

            modals.confirm({
                title: t(`pages.showcase.${ code }.unsaved.confirm.title`),
                body: t(`pages.showcase.${ code }.unsaved.confirm.description`),
                onOk: async() => {
                    delete data.hasChanges;

                    callback();
                }
            }); 
            return;
        }

        callback();
    }

    const onUpdate = (payload) => {
        if (data.section === 'sections') {
            showcaseSections.value.update(payload)
        }

        if (data.section === 'widgets') {
            showcaseWidgets.value.update(payload)
        }

        data.item = payload;

        delete data.hasChanges;
    }

    const onEdit = () => {
        data.hasChanges = true;
    }

    const onAdd = (payload) => {
        if (data.section === 'sections') {
            showcaseSections.value.add(payload)
        }

        if (data.section === 'widgets') {
            showcaseWidgets.value.add(payload)
        }
        data.item = payload;

        delete data.hasChanges;
    }

    const onRemove = (id) => {
        if (!id) {
            return;
        }

        if (data.section === 'sections') {
            showcaseSections.value.remove(id);
        }

        if (data.section === 'widgets') {
            showcaseWidgets.value.remove(id);
        }

        delete data.hasChanges;
        close();        
    }
</script>

<style lang="scss" scoped>
    @include studio.default-box;

    .header {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 48px;
        .title {
            font: var(--font-h4);
            font-weight: bold;
            color: var(--dark-500);
        }
    }
</style>
